<template>
  <transition name="modal-fade">
    <div class="modal__overlay">
      <div class="modal">
        <div class="modal__header">
          <slot name="header"></slot>

          <button
            @click="close"
            class="modal__close"
            type="button"
          >
            <svg
              width="16"
              height="16"
            >
              <use xlink:href="#icon-close"></use>
            </svg>
          </button>
        </div>
        
        <div class="modal__body">
          <slot name="body"></slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Modal',

  mounted() {
    window.addEventListener('keydown', this.keydown);
  },

  beforeDestroy() {
    window.removeEventListener('keydown', this.keydown);
  },

  methods: {
    keydown(e) {
      if (e.keyCode === 27) {
        this.close();
      }
    },

    close() {
      this.$emit('close');
    }
  }
}
</script>

<style lang="less" scoped>
.modal {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 600px;
  overflow-x: auto;

  background: #ffffff;
  border-radius: 4px;
  box-shadow: 2px 2px 20px 1px rgba(0, 0, 0, 0.5);

  &__overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;

    background-color: rgba(0, 0, 0, 0.3);
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;

    color: #000000;

    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

  &__close {
    margin-left: auto;
    padding: 0.25rem;

    background-color: transparent;
    border: none;
    cursor: pointer;

    svg {
      display: block;

      fill: currentColor;
    }
  }

  &-fade-enter,
  &-fade-leave-to {
    opacity: 0;
  }

  &-fade-enter-active,
  &-fade-leave-active {
    transition: opacity 0.3s ease;
  }
}
</style>