<template>
  <Modal
    v-if="open"
    @close="close"
  >
    <template v-slot:header>
      <h1 class="confirmation__heading heading">Confirmation</h1>
    </template>

    <template v-slot:body>
      <div class="confirmation__text">
        <slot name="text"></slot>
      </div>

      <div class="confirmation__actions">
        <button
          @click="close"
          class="confirmation__cancel button"
          type="button"
        >No</button>

        <button
          @click="agree"
          class="confirmation__agree button"
          type="button"
        >Yes</button>
      </div>
    </template>
  </Modal>
</template>

<script>
import Modal from '@/components/Modal';

export default {
  name: 'Confirmation',

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },

  components: {
    Modal
  },

  methods: {
    close() {
      this.$emit('close');
    },

    agree() {
      // CLOSE CONFIRM MODAL
      this.close();

      // EMIT AGREE EVENT
      this.$emit('agree');
    }
  }
}
</script>

