  &__text {
    padding: 1rem;
  }

  &__actions {
    display: flex;
    justify-content: flex-end;
    padding: 1rem;

    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }

  &__cancel {
    margin-right: 0.5rem;
  }
}
</style>